body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
}

h1 {
  text-align: center;
}

form {
  margin-bottom: 20px;
}

form label {
  margin-bottom: 5px;
  display: block;
}

form input[type="text"], form input[type="number"], form button {
  width: 100%;
  margin-bottom: 10px;
  padding: 8px;
  font-size: 16px;
}

form button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
}

form button:hover {
  background-color: #45a049;
}

#todo-list {
  padding: 0;
  list-style-type: none;
}

#todo-list li {
  background-color: #f9f9f9;
  border: 1px solid #ccc;
  margin-bottom: 10px;
  padding: 10px;
}

#todo-list li button {
  color: #fff;
  cursor: pointer;
  background-color: #f44336;
  border: none;
  margin-left: 10px;
}

#todo-list li button:hover {
  background-color: #d32f2f;
}
